import React from 'react'

interface InfoProps {
  image: string;
  title: string;
  text: string;
}

const Info: React.FC<InfoProps> = ( {image, title, text} ) => {
  return (
    <div className='info'>
        <div className="info__image">
            <img src={image} alt="Empty" />
        </div>
        <h2 className='info__title'>{title}</h2>
        <p className='info__text'>{text}</p>
    </div>
  )
}

export default Info